@import url("https://fonts.googleapis.com/css?family=Playfair+Display|Roboto&display=swap");
/* start: global styles */
body {
  font-family: "Roboto", "Helvetica", "Sans-serif";
  margin: 0;
  padding: 0;
  font-size: 1rem;
  font-weight: 400;
  color: #777;
  line-height: 1.7;
}

* {
  outline: none;
}

*:not(.slick-track):not(.slick-list) {
  transition: 0.4s;
}

.container {
  max-width: 1140px;
  margin: 0 auto;
  padding: 0 20px;
}

.flex {
  display: flex;
  justify-content: space-between;
}

h1,
h2,
h3,
h4,
h5 {
  margin: 0;
  padding: 0;
  font-family: "Playfair Display", serif;
}

h1,
h2 {
  font-weight: 800;
}

h1 {
  font-size: 3rem;
  line-height: 1.2;
  margin-bottom: 1rem;
}

h2 {
  font-size: 2.5rem;
  color: #bfae9f;
  margin-bottom: 1rem;
  line-height: 1.2;
}

h3 {
  font-size: 1.4rem;
  font-weight: 300;
}

h4 {
  font-size: 1.2rem;
  font-weight: 300;
}

h5 {
  font-size: 1rem;
}

p {
  margin: 0 0 1rem;
}

.link {
  color: #bfae9f;
  text-decoration: none;
}

button.rounded {
  background: #bfae9f;
  border: none;
  color: #fff;
  font-size: 16px;
  border-radius: 30px;
  padding: 10px 30px;
  line-height: 1.5;
  cursor: pointer;
}

/* end: global styles */
/* start: header styles */
#header-hero-container {
  min-height: 600px;
  height: 100vh;
  position: relative;
}
#header-hero-container header {
  background: transparent;
  padding: 1rem 0;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  position: fixed;
  width: 100%;
  z-index: 3;
}
#header-hero-container header span.fas,
#header-hero-container header span.fab {
  color: #bfae9f;
  font-size: 20px;
  padding: 10px;
}
#header-hero-container #logo {
  font-family: "Roboto", "Helvetica", "Sans-serif";
  font-size: 1.5rem;
  font-weight: 800;
  color: #fff;
  text-decoration: none;
  position: relative;
  top: 4px;
}

.hamburger-menu {
  background-color: transparent;
  border: 1px solid #fff;
  border-radius: 5px;
  padding: 3px 5px;
  width: 30px;
  cursor: pointer;
  margin-top: 10px;
  display: none;
}

.hamburger-menu .strip {
  display: block;
  height: 1px;
  background-color: #fff;
  margin: 4px 0;
}

header ul {
  list-style-type: none;
}

header ul li {
  display: inline-block;
  margin: 0 15px;
}

#close-flyout {
  display: none;
}

header ul li a {
  color: #999;
  font-size: 16px;
  text-decoration: none;
}

header ul.is-sticky li a {
  color: #000;
}

header ul li a:hover,
header ul li a.active {
  color: #fff;
}

header ul.is-sticky li a:hover,
header ul.is-sticky li a.active {
  color: #bfae9f;
}

/* end: header styles */
/* start: hero section styles */
#hero {
  background: url("images/bg.jpg") center center no-repeat;
  background-size: cover;
  background-attachment: fixed;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  height: 100%;
  width: 100%;
}
#hero .fade {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.5));
  z-index: 1;
}
#hero .hero-text {
  position: absolute;
  top: 50%;
  margin: -75px auto 0;
  left: 0;
  right: 0;
  z-index: 2;
  color: #fff;
  width: 500px;
  text-align: center;
}
#hero .hero-text p {
  line-height: 1.5em;
  font-weight: 300;
  font-size: 1rem;
}

/* end: hero section styles */
/* start: how it works styles */
#covid-19,
#location,
#event,
#contact {
  padding: 3rem 0;
  text-align: center;
}
#covid-19 .container,
#location .container,
#event .container,
#contact .container {
  padding: 50px;
}
#covid-19 h2,
#location h2,
#event h2,
#contact h2 {
  margin-bottom: 3rem;
}
#covid-19 .flex > div,
#location .flex > div,
#event .flex > div,
#contact .flex > div {
  width: 30%;
}
#covid-19 h4,
#location h4,
#event h4,
#contact h4 {
  color: #343a40;
  margin-top: 1rem;
}

#event .dot {
  height: 50px;
  width: 50px;
  background-color: red;
  border-radius: 50%;
  display: inline-block;
}
#event .heart {
  display: inline-block;
  width: 50px;
  aspect-ratio: 1;
  clip-path: polygon(-41% 0, 50% 91%, 141% 0);
}
#event .nude_1 {
  border-image: radial-gradient(#f1dcc9 69%, #0000 70%) 84.5% fill/100%;
}
#event .nude_2 {
  border-image: radial-gradient(#c09175 69%, #0000 70%) 84.5% fill/100%;
}
#event .nude_3 {
  border-image: radial-gradient(#bd8270 69%, #0000 70%) 84.5% fill/100%;
}
#event .nude_4 {
  border-image: radial-gradient(#e7bab4 69%, #0000 70%) 84.5% fill/100%;
}

#covid-19 .covid-19-intro {
  margin-bottom: 8rem;
  text-align: center;
}
#covid-19 .covid-19-intro blockquote {
  font-size: 1.3rem;
  font-style: italic;
  margin: 1rem auto 3rem;
  max-width: 740px;
}

/* end: how it works styles */
#location .container {
  background: #f8f9fa;
}
#location .container a {
  color: #bfae9f;
}
#location .container a:link,
#location .container a:visited {
  text-decoration: underline;
}
#location .container a:active,
#location .container a:hover {
  text-decoration: none;
}

/* start: blog rss feed styles */
#blog {
  background: #f8f9fa;
  padding: 3rem 0;
  text-align: center;
}
#blog .container a {
  color: #bfae9f;
}
#blog .container a:link,
#blog .container a:visited {
  text-decoration: none;
}
#blog .container a:active,
#blog .container a:hover {
  text-decoration: underline;
}
#blog .container .title {
  font-size: 1.4rem;
  color: #bfae9f;
  font-weight: 800;
  margin: 0 0 0.5rem 0;
}
#blog .container .posts {
  margin: 0 4rem;
  margin-top: 1rem;
}
#blog .container .posts .date {
  font-style: italic;
  color: #777;
}
#blog .container .posts img {
  margin-left: auto;
  padding-right: 0.5rem;
  border-radius: 50%;
  width: 100px;
  height: 100px;
  object-fit: cover;
}
#blog .container .posts .project-details {
  padding-bottom: 1rem;
}
#blog .container .posts .project-details .description {
  color: #777;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 4;
  /* number of lines to show */
  -webkit-box-orient: vertical;
}

/* end: blog rss feed styles */
/* start: contact us styles */
#contact {
  background: #f8f9fa;
  padding: 4rem 0 8rem;
}
#contact h2 {
  text-align: center;
  margin-bottom: 4rem;
}
#contact .flex {
  justify-content: center;
}
#contact .flex #form-container {
  width: 90%;
  background: #fff;
  padding: 3rem;
  box-sizing: border-box;
  height: 0%;
}
#contact .flex #form-container h3 {
  color: #000;
  margin-bottom: 2rem;
}
#contact .flex #form-container label {
  display: block;
  color: #000;
  margin-top: 1rem;
}
#contact .flex #form-container input,
#contact .flex #form-container textarea {
  display: block;
  border-radius: 20px;
  padding: 10px;
  border: 1px solid #ced4da;
  width: 100%;
  box-sizing: border-box;
  height: 43px;
  font-family: "Roboto", "Helvetica", "Sans-serif";
  font-size: 1rem;
  color: #495057;
}
#contact .flex #form-container textarea {
  height: 170px;
  margin-bottom: 2rem;
  resize: none;
}
#contact .flex #form-container email-submit {
  justify-content: center;
}

/* end: contact us styles */
/* start: footer styles */
footer {
  background: #333;
  padding: 4rem 0;
}
footer .container {
  text-align: center;
}
footer .container span.fas,
footer .container span.fab {
  color: #bfae9f;
  font-size: 25px;
  padding: 15px;
}
footer .container small {
  margin-top: 3rem;
  font-size: 1rem;
  display: block;
  text-align: center;
}

/* end: footer styles */
/* start: media queries */
@media (max-width: 991px) {
  #logo {
    top: 1px;
  }

  #nav-toggle {
    display: block;
  }

  nav ul {
    position: fixed;
    background: #fff;
    right: -100%;
    top: 0;
    margin: 0;
    padding: 2rem;
    height: 100%;
    width: 300px;
    box-sizing: border-box;
    box-shadow: 5px 0 10px 3px #999;
  }

  #close-flyout {
    position: absolute;
    right: 2rem;
    top: 1.5rem;
    font-size: 30px;
    color: #777;
    cursor: pointer;
    display: block;
    line-height: 1;
    margin: 0;
  }

  nav ul li {
    display: block;
    margin: 20px 0;
  }

  nav ul li:first-child {
    margin-top: 50px;
  }

  nav ul li a {
    font-size: 20px;
    color: #212529;
  }

  nav ul li a.active,
nav ul li a:hover {
    color: #bfae9f;
  }
}
@media (max-width: 767px) {
  h1 {
    font-size: 2rem;
  }

  h2 {
    font-size: 1.75rem;
  }

  h3 {
    font-size: 1.2rem;
  }

  .large-paragraph {
    font-size: 1rem;
  }

  p {
    font-size: 0.8rem;
  }

  .container {
    max-width: 540px;
  }

  #hero .hero-text {
    width: 400px;
  }

  #projects .container:before {
    top: 2px;
    font-size: 0.85rem;
  }

  #projects-slider a.slick-arrow {
    font-size: 0.85rem;
  }

  #projects button.rounded {
    width: 300px;
  }

  #contact .flex {
    flex-wrap: wrap;
  }

  #form-container {
    width: 100%;
    padding: 2rem;
  }

  footer {
    padding: 6rem 0;
  }
}
@media (max-width: 500px) {
  #hero .hero-text {
    width: 330px;
  }

  #agents {
    margin: 4rem 0 3rem;
    padding: 0 0 3rem;
  }

  #agents .card {
    width: 100%;
  }

  #agents p.large-paragraph {
    width: 100%;
  }

  #projects button.rounded {
    width: 250px;
  }

  #services {
    padding: 4rem 0 3rem;
    margin: 4rem 0;
  }

  #form-container {
    padding: 1.5rem;
  }

  footer {
    padding: 4rem 0;
  }
}
@media (max-width: 400px) {
  #hero .hero-text {
    max-width: 275px;
    width: 100%;
  }

  #projects button.rounded {
    max-width: 250px;
    width: 100%;
  }

  nav ul {
    width: 200px;
  }
}
/* end: media queries */